<template>

    <div class="row">
        <div class="col-md-12 padding-t_8">
            <div class="theme-primary partition-full">
                <span class="p-name text-white"><em class="fa fa-database"></em>  Storage</span>
            </div>
            <div class="border p-3">
                <div class="row">
                    <div class="col-lg-6">
                        <div class="form-group">
                            <label>{{ $t('Storage') }}</label><span class="text-danger">*</span>
                            <input type="text" id="SizeDocStorage" class="form-control" v-model="SizeDocStorage" />                            
                        </div>
                    </div>
                    <div class="col-lg-6">
                        <label>{{ $t('RetentionRecyle') }}</label><span class="text-danger">*</span>
                        <input type="text" id="RetentionValue" class="form-control" v-model="RetentionValue" />                       
                    </div>
                    <div class="col-lg-6">
                        <label>{{ $t('FileType') }}</label><span class="text-danger">*</span>
                        <input type="text" id="FileTypeValue" class="form-control" v-model="FileTypeValue" />
                    </div>
                </div>              

                <div class="text-right ">
                    <button title="Save" @click="SaveStorage()"
                        class="popupgreenbtn btn btn-success formbtn widthhalf pr-2"><em aria-hidden="true"
                            class="fa fa-save pr-2"></em> Save</button>
                    &nbsp;
                    <button title="Cancel" @click="CancelStorage()"
                        class="popupredbtn btn btn-danger formbtn widthhalf pr-2"><em aria-hidden="true"
                            class="fa fa-times pr-2"></em> Cancel</button>
                </div>
            </div>
        </div>
    </div>

</template>
    
<script>
import DataService from "../../services/DataService"
export default {
    data() {
        return {
            date: '',
            size: '',
            SizeDocStorage: null,
            RetentionValue: null,
            FileTypeValue:null,
            SizeConfigDataId: null,
            RetentionConfigDataId: null
        }
    },
    created: function () {
        this.GetStorageDetail();
    },
    methods: {
        SaveStorage: function () {
            let objConfigData = [{
                CONFIG_DATA_ID: this.SizeConfigDataId,
                CONFIG_DATA_VALUE: this.SizeDocStorage
            },
            {
                CONFIG_DATA_ID: this.RetentionConfigDataId,
                CONFIG_DATA_VALUE: this.RetentionValue
            }
            ]
            var jsonObj = JSON.stringify(objConfigData);
            var params = `jsonData=${jsonObj}&refCode=DocSetting`;
            DataService.ManageDocUserStorageSettingData(params).then((response) => {                
                if (response.data == 0) {
                    this.ShowAlert("Storage Configuration Data Updated Successfully")
                }
                else {
                    vm.ShowAlert(vm.$t("UnknownErrorOccur"), "failure", true, vm.$t('Alert'));
                }
            });
        },
        CancelStorage: function () {
            alert("Cancel")
        },
        GetStorageDetail: function () {
           // debugger;
            var vm = this; 
            var params = `moduleName=DocStorage&keyValue=SizePerDoc,RetentionDays,DocStorageAllowedFileTypes`;             
            DataService.GetConfigDataValue(params).then((response) => { 
                vm.isLoading = true; 
                vm.FileTypeValue = JSON.parse(response.data.ConfigValue)[0].CONFIG_DATA_VALUE;
                vm.RetentionValue =  JSON.parse(response.data.ConfigValue)[1].CONFIG_DATA_VALUE;
                vm.SizeDocStorage = JSON.parse(response.data.ConfigValue)[2].CONFIG_DATA_VALUE;
                vm.fileTypeFormats = vm.fileType.split(','); 
                vm.isLoading = false;
             }) 
        }
    }
}
</script>